
import { defineComponent, onMounted } from "vue";
import ChartsWidget1 from "@/components/widgets/charts/Widget1.vue";
import { setCurrentPageBreadcrumbs } from "@/core/helpers/breadcrumb";
import axios from "axios";
import TimelineIndicateur from "@/components/widgets/charts/timelineIndicateur.vue";

export default defineComponent({
  name: "widgets-chart",
  components: {
    TimelineIndicateur,
    ChartsWidget1,
  },
  data() {
    return {
      test:
        process.env.VUE_APP_API == "x"
          ? process.env.VUE_APP_API
          : process.env.VUE_APP_API,
      years: [Number(new Date().getFullYear())],
      year: Number(new Date().getFullYear()),
      dialog: false,
      chartData: {
        theme: "",
        chartName: "",
        barColor: "",
      },
      chartDatas: [],
      widgetKey: 0,
    };
  },
  watch: {
    year(newValue) {
      this.widgetKey++;
    },
  },
  setup() {
    onMounted(() => {
      setCurrentPageBreadcrumbs("Indicateurs Environementaux", ["Widgets"]);
    });
  },
  computed: {
    currentRouteName() {
      return process.env.VUE_APP_API;
    },
  },
  methods: {
    createChartData() {
      axios
        .post(this.currentRouteName + "/api/chart-data", this.chartData)
        .then((response) => {
          location.reload();
          console.log(response.data);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    async fetch() {
      const chart = await axios.get(this.currentRouteName + "/api/chart-data");
      this.chartDatas = chart.data;
      const years: number[] = chart.data.flatMap((chart) =>
        chart.month_values.map((value) => value.year)
      );
      this.years = [...new Set(years)];
    },
  },
  mounted() {
    this.fetch();
  },
});
